 import dyDatePicker from '../../common/dy-Date.vue'
export default{
	data(){
		return{
			flag:false,
			date:'本月',
			num:'0',
			ber:'',
			nuber:'',
			fee:'',
			entin:'',
			teran: '',
			month_profit:'0',
			profit_records:[],
			month_bill:'',
			bill_records:[],
			length:'',
			length1:''
		}
	},
	components: {
		dyDatePicker
	},
	onShow:function () {
	    var that = this
	    var data = new Date()
	    var text = data.getFullYear('-')
	    var txt = data.getMonth()+1
	    var teran = text + '-' + txt
	    that.teran = teran
	    uni.request({
	        url:this.url + "assets/",
	        method:'GET',
	        header:{
	            Authorization:'JWT'+' '+this.global_.token
	        },
	        success(res) {
	            that.num = res.data.availed_num
	            that.ber = res.data.fil_count
	            that.nuber = res.data.locked_num
				that.fee=res.data.fee
	        }
	    })
	   uni.request({
			url: this.url + 'assets/month/profit/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			data: {
				month: teran
			},
			success(res) {
				that.month_profit = res.data.data.month_profit
				that.profit_records = res.data.data.profit_records
				that.length=res.data.data.profit_records.length
			}
	   })
	   uni.request({
			url: this.url + 'assets/month/bill/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			data: {
				month: teran
			},
			success(res) {
				that.month_bill = res.data.data.month_bill
				that.bill_records = res.data.data.bill_records
				that.length1=res.data.data.bill_records.length	
				if(that.length==0 && that.length1==0){
					that.flag=true
				}else{
					that.flag=false
				}
			}
	   })
	},
	methods: {
		DateChang(e) {
			var that = this
			that.date = e
			uni.request({
				url: this.url + 'assets/month/profit/',
				method: 'GET',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				data: {
					month: e
				},
				success(res) {
					that.month_profit = res.data.data.month_profit
					that.profit_records = res.data.data.profit_records
					that.length=res.data.data.profit_records.length
					if(that.length==0 && that.length1==0){
						that.flag=true
					}else{
						that.flag=false
					}
				}			
			})
			uni.request({
				url: this.url + 'assets/month/bill/',
				method: 'GET',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				data: {
					month: e
				},
				success(res) {
					that.month_bill = res.data.data.month_bill
					that.bill_records = res.data.data.bill_records
					that.length1=res.data.data.bill_records.length	
					if(that.length==0 && that.length1==0){
						that.flag=true
					}else{
						that.flag=false
					}
				}			
			})
		},
	    transfer:function(){
			uni.navigateTo({
				url:'../transfer/transfer?bar='+ this.num+'&fee='+this.fee
			})
		}
	}
}
